import React from 'react';
import EntryText from './entrytext';
import EntryDate from './entrydate';

const JourneyEntry = (props) => {
    const entry = (props.entry ? props.entry : {})
    const dir = (props.on_left ? "left" : "right")
    const retlist = [
        <EntryText text={entry.text} subtitle={entry.subtitle} />,
        <div className="journeyicon">
            <a href={entry.url} target="_blank" rel="noreferrer">
                <img className="iconimg" src={entry.img} alt={entry.text} />
            </a>
        </div>,
        <EntryDate dir={dir} date={entry.date} />
    ]
    return (
        <div className="journeyentry">
            {
                props.on_left ? retlist.map(val => val) : retlist.reverse().map(val => val)
            }
        </div>
    )
}
export default JourneyEntry;
