import React from 'react';

const AchieveItem = (props) => {
    const color = (props.color ? props.color : props.def)
    return (
        <h1
            className={`itemname ${props.hide ? "hide" : ""}`}
            onClick={() => { props.setID(props.id) }}
            onMouseOver={(e) => { e.target.style.color = color; }}
            onMouseLeave={(e) => { e.target.style.color = props.def; }}
        >
            {props.title}
        </h1 >
    )
}
export default AchieveItem;
