import React from 'react';
import QAPair from './qapair';

const AboutMeText = () => {
    const QandA = (q, a) => {
        return (
            {
                q: q,
                a: a
            }
        )
    }
    const QA_1 = QandA(
        '"Who Am I?"',
        "My name is Nicholas Raffone, and I'm a Italian Japanese undergrad sophomore with a passion for computing!"
    )
    const QA_2 = QandA(
        '"What Motivates Me?"',
        "I like to stay motivated by being present in my activities everyday. By being attentive to small details when I pen spin, listen to indie music, or go on urban hikes, I'm able to find topics I want to explore and learn about the world around me."
    )
    const QA_3 = QandA(
        '"What\'s My Biggest Dream?"',
        "At some point I want to open a cafè to host a variety of events and interact with the local community."
    )
    const QAlist = [
        QA_1,
        QA_2,
        QA_3
    ]
    return (
        <div className="aboutmetext">
            {
                QAlist.map((QA) => {
                    return (
                        <QAPair question={QA.q} answer={QA.a} />
                    )
                })
            }
        </div>
    )
}
export default AboutMeText;