import React from 'react';

const AchievementContent = (props) => {
    return (
        <div className="achievementcontent" style={{ 'background-color': props.entry.color }}>
            <div className="achievementimg"><img src={props.entry.img} alt={props.entry.title} /></div>
            <div className="achievementtext">
                <h2>{props.entry.title}</h2>
                <h3>{props.entry.desc} {props.entry.link !== "" ? <a href={props.entry.link} target="_blank">Click here for more information.</a> : ''}</h3>
                <h3>Skills: {props.entry.skills}</h3>
            </div>
        </div>
    )
}
export default AchievementContent;
