import React, { useState } from 'react';
import '../styles/achievements.css'
import album from '../images/album.png'
import ham from '../images/ham.png'
import robo2 from '../images/robo2.JPEG'
import dis from '../images/dis.png'
import site from '../images/site.png'
import paper from '../images/paper.png'
import AchieveItem from './achieveitem'
import AchievementContent from './achievementcontent'

const Achievements = () => {
    const [id, setID] = useState(0);
    const [hide, setHide] = useState(false);
    const achievement = (item, title, desc, skills, color, img, link) => {
        return ({
            item: item,
            title: title,
            desc: desc,
            skills: skills,
            color: color,
            img: img,
            link: link
        })
    }
    const ml_research = achievement(
        "ML Research",
        "Speculating Success: An Experimental Study",
        "I wrote a peer-reviewed piece on improving the accuracy of neural networks compared to others in the current literature. Implementing dropout and adaptive optimisation resulted in a 10% increase in accuracy when predicting student performance.",
        "Machine Learning, Research, Python",
        'rgb(215, 230, 240)',
        paper,
        "https://drive.google.com/file/d/1nu4-eKFU-h_owx6v6MK5llFAxMP7oKYO/view?usp=sharing"
    )
    const earthquake = achievement(
        "Earthquake Notifier",
        "Earthquake Notifier",
        "The Earthquake Notifier is an AWS Serverless app I built at Hokan to relay real time natural disaster information that may have affected clients for the Customer Success team to follow up if deemed necessary. Information is received from government sources and set through Slack.",
        "AWS SAM, API Programming, Python",
        'rgb(240, 219, 255)',
        dis,
        ""
    )
    const daily = achievement(
        "Daily Site Diff Checker",
        "Daily Site Diff Checker",
        "The Daily Site Diff Checker is an AWS Serverless app designed to easily understand the difference between a site at two different periods of time. A user is able to check diffs on a daily basis or retrieve an HTML page containing page differences via a modal in Slack.",
        "AWS SAM, Web Scraping, Python",
        'rgb(220, 255, 225)',
        site,
        ""
    )
    const genreguessing = achievement(
        "Album Art Quiz",
        "Album Art Quiz",
        "For music enjoyers: The Album Art Quiz is a game made in React where upon picking a genre, the page will have you guess 10 album names based on pixelated art and a color palette. Users log in using their Spotify credentials which is then used to access the Spotify API.",
        "React JS, HTML, CSS, API Programming",
        'rgb(212, 223, 252)',
        album,
        "https://nicholasraffone.github.io/album-game/"
    )
    const roboevents = achievement(
        "Robotics Events",
        "Robotics Events",
        "As a co-captain and lead programmer of VEX Robotics team 901J, I lead my team of 7 students to qualify for the VEX World Championships two years in a row. Earning 6 trophies, we surpassed the ASIJ record for number of awards attained in a season and won 2 international events.",
        "Leadership, C++, Teamwork",
        'rgb(220, 219, 255)',
        robo2,
        ""
    )
    const achievement_list = [
        ml_research,
        genreguessing,
        daily,
        roboevents,
        earthquake,
    ]
    const default_color = 'rgb(51, 51, 51)'

    return (
        <div className="achievements">
            <div className="items">
                <div className="achievementtitlewrapper">
                    <h1 className="achievementtitle">
                        Achievements and Projects
                    </h1>
                    <div>
                        <img className="expandbutton" src={ham} onClick={() => setHide(!hide)} alt="expand" />
                    </div>
                </div>
                {
                    achievement_list.map((val, i) => {
                        return (<AchieveItem
                            id={i}
                            setID={(v) => setID(v)}
                            title={val.item}
                            def={default_color}
                            color={val.color}
                            hide={hide}
                        />)
                    })
                }
            </div>
            <div className="display">
                <AchievementContent
                    entry={achievement_list[id]}
                />
            </div>
        </div>
    )
}
export default Achievements;
