import React from 'react';

const SocialIcons = (props) => {
    return (
        <div className="socialicons">
            {
                props.icons.map(val => {
                    return (
                        <a href={val.url} target="blank_">
                            <img className="socialicon" src={val.img_url} alt={val.url} />
                        </a>
                    )
                })
            }
        </div>
    )
}

export default SocialIcons;
